import React, { useEffect, useRef } from 'react';
import './index.less';

export default function Bouncing3DLoading() {
  const splitSentence = () => {
    const { current } = text;
    const letters = current.textContent.split('');
    current.textContent = '';
    letters.forEach((letter, i) => {
      const span = document.createElement('span');
      span.textContent = letter;
      span.style.animationDelay = `${i * 0.1}s`;
      current.appendChild(span);
    });
  }

  const text = useRef();
  useEffect(splitSentence, []);

  return (
    <section className="bouncing-3d-loading">
      <h4>Bouncing3DLoading</h4>
      <main>
        <div className="loading" ref={text}>loading</div>
      </main>
    </section>
  )
}
