import React, { useEffect, useRef } from 'react';
import './index.less';

export default function MilkScratch() {

  const cardNode = useRef();
  useEffect(() => {
    const milkCount = 100;
    for (let i = 0; i < milkCount; i++) {
      const milk = document.createElement('div');
      milk.classList.add('milk');
      cardNode.current.appendChild(milk);
    }
  }, []);

  return (
    <section className="milk-scratch">
      <h4>Milk Scratch</h4>
      <main>
        <p>Hello</p>
        <div className="card" ref={cardNode}></div>
      </main>
    </section>
  )
}
