import React, { useEffect, useRef } from 'react';
import './index.less';

export default function Gauge() {
  
  const gaugeListNode = useRef();
  useEffect(() => {
    const gaugeList = gaugeListNode.current.querySelectorAll('.gauge-content');
    gaugeList.forEach(item => {
      item.style.setProperty('--percentage', item.dataset.percentage + '%');
    })
  },[]);

  return (
    <section className="gauge">
      <h4>Gauge</h4>
      <main>
        <div className="gauge-list" ref={gaugeListNode}>
          <div className="gauge-item">
            <div className="gauge-content" data-percentage={56}></div>
            <span className="gauge-title">design</span>
          </div>
          <div className="gauge-item" data-percentage>
            <div className="gauge-content" data-percentage={78}></div>
            <span className="gauge-title">creativity</span>
          </div>
          <div className="gauge-item">
            <div className="gauge-content" data-percentage={90}></div>
            <span className="gauge-title">content</span>
          </div>
        </div>
      </main>
    </section>
  )
}
