import React, { useState, useRef} from 'react';
import { NavLink } from 'react-router-dom';
import './index.less';

export default function LeftSideMenu() {
    
  const [hasShowMenu, setHasShowMenu] = useState(false);
  const leftSideMenu = useRef();

  const showMenu = () => {
    if (hasShowMenu) {
      leftSideMenu.current.classList.remove('left-side-menu-active');
    } else {
      leftSideMenu.current.classList.add('left-side-menu-active');
    }
    setHasShowMenu(!hasShowMenu);
  }

  return (
    <React.Fragment>
      <div className="left-side-menu" ref={leftSideMenu} onClick={showMenu}>
        <NavLink className="nav" to="/ProfileCard">Profile Card</NavLink>
        <NavLink className="nav" to="/WaveLoading">Wave Loading</NavLink>
        <NavLink className="nav" to="/LandInText">LandIn Text</NavLink>
        <NavLink className="nav" to="/RevealText">Reveal Text</NavLink>
        <NavLink className="nav" to="/Snow">Snow</NavLink>
        <NavLink className="nav" to="/ButtonBorderStroke">Button Border Stroke</NavLink>
        <NavLink className="nav" to="/HeaderWithSlideBar">Header With Slide Bar</NavLink>
        <NavLink className="nav" to="/FloatTextMenu">Float Text Menu</NavLink>
        <NavLink className="nav" to="/ButtonHoverShining">Button Hover Shining</NavLink>
        <NavLink className="nav" to="/TodoList">Todo List</NavLink>
        <NavLink className="nav" to="/NavTab">Nav Tab</NavLink>
        <NavLink className="nav" to="/Pagination">Pagination</NavLink>
        <NavLink className="nav" to="/CrimsonCrescentLoading">Crimson Crescent Loading</NavLink>
        <NavLink className="nav" to="/NeonText">Neon Text</NavLink>
        <NavLink className="nav" to="/Bouncing3DLoading">Bouncing 3D Loading</NavLink>
        <NavLink className="nav" to="/MenuHoverFillText">Menu Hover Fill Text</NavLink>
        <NavLink className="nav" to="/ConfirmModal">Confirm Modal</NavLink>
        <NavLink className="nav" to="/ParticleButton">Particle Button</NavLink>
        <NavLink className="nav" to="/Gauge">Gauge</NavLink>
        <NavLink className="nav" to="/MilkScratch">Milk Scratch</NavLink>
        <NavLink className="nav" to="/FrostedGlass">Frosted Glass</NavLink>
        <NavLink className="nav" to="/NameCard">Name Card</NavLink>
        <NavLink className="nav" to="/CrossBarGlitchText">Cross Bar Glitch Text</NavLink>
        <NavLink className="nav" to="/RotateCircles">Rotate Circles</NavLink>
      </div>
      <div className="menu-btn" onClick={showMenu}></div>
    </React.Fragment>
  )
}
