import React, { useEffect } from 'react';
import './index.less';

export default function RotateCircles() {
  useEffect(() => {
    try {
      CSS.registerProperty({
        name: '--color1',
        syntax: '<color>',
        initialValue: 'transparent',
        inherits: true
      });
    
      CSS.registerProperty({
          name: '--color2',
          syntax: '<color>',
          initialValue: 'transparent',
          inherits: true
      });
    
      CSS.registerProperty({
          name: '--pos',
          syntax: '<length-percentage>',
          initialValue: '0',
          inherits: true
      })
    } catch {
      
    }
  }, []);

  return (
    <section className="rotate-circles">
      <h4>Rotate Circles</h4>
      <main>
        <div className="circle"></div>
        <div className="circle"></div>
        <div className="circle"></div>
        <div className="circle"></div>
        <div className="circle"></div>
        <div className="circle"></div>
        <div className="circle"></div>
        <div className="circle"></div>
      </main>
    </section>
  )
}
