import React, { useEffect, useRef } from 'react';
import './index.less';

export default function ParticleButton() {
  // 生成泡泡粒子
  const generateBubbles = () => {
    const bubblesCount = 16;
    const bubbleTypes = ['var(--bubble-one)', 'var(--bubble-two)'];
    let backgroundImage = [];
    let backgroundSize = [];
    // 随机生成两种类型的泡泡和大小
    for (let i = 0; i < bubblesCount; i++) {
      backgroundImage.push(bubbleTypes[Math.floor(Math.random()*1.999)]);
      backgroundSize.push(`${Math.floor(Math.random()*10+10)}% ${Math.floor(Math.random()*10+10)}%`);
    }

    const styleNode = document.createElement('style');
    document.getElementsByTagName('head')[0].appendChild(styleNode);
    const style = document.styleSheets[document.styleSheets.length - 1];
    style.insertRule(`
      .btn-bubbles .bubbles {
        background-image: ${backgroundImage.toString()};
        background-size: ${backgroundSize.toString()};
      }
    `, style.cssRules.length);
  }

  const bubbles = useRef();
  useEffect(generateBubbles, []);

  return (
    <section className="particle-button">
      <h4>Particle Button</h4>
      <main>
        <button className="btn btn-pink btn-bubbles">
          <div className="bubbles" ref={bubbles}></div>
          Click Me
        </button>
      </main>
    </section>
  )
}
