import React, { useEffect, useRef } from 'react'
import './index.less';

export default function CrossBarGlitchText() {

  const splitText = () => {
    const content = text.current.textContent;
    text.current.textContent = '';

    const glitchText = document.createElement('div');
    glitchText.className = 'glitch';
    glitchText.style.setProperty('--slice-count', 20);
    for (let i = 0; i < 20; i++) {
      const span = document.createElement('span');
      span.textContent = content;
      span.style.setProperty('--i', i+1);
      glitchText.appendChild(span);

      if (i !== 0) {
        span.style.animationDelay = `${Math.random() * 200 + 900}ms`;
      }
    }
    text.current.appendChild(glitchText);

    const bars = document.createElement('div');
    bars.className = 'bars';
    for (let i = 0; i < 5; i++) {
      const bar = document.createElement('div');
      bar.className = 'bar';
      bars.appendChild(bar);
    }
    text.current.appendChild(bars);
  }

  const text = useRef();
  useEffect(splitText, []);

  return (
    <section className="cross-bar-glitch-text">
      <h4>Cross Bar Glitch Text</h4>
      <main>
        <div className="cross-bar-glitch"  ref={text}>steins;Gate</div>
      </main>
    </section>
  )
}
